import AppBar from '@mui/material/AppBar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import { useState } from 'react';
import TabPanel from '../atomic/tabPanel';
import EditProfile from './profile';
import EditEducation from './education';
import EditSkills from './skills';
import EditExpertise from './expertise';
import EditAchievements from './achievements';
import EditWorkExperience from './experience';

export default function EditResumeComponent() {
    const [value, setValue] = useState(0);

    const handleChange = (event: any, newValue: number) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ bgcolor: 'background.paper', width: '100%' }} >

            <AppBar position="static" sx={{ backgroundColor: "grey" }}>
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="secondary"
                    textColor="inherit"
                    variant="fullWidth"
                    aria-label="full width tabs example"
                >                  
                    <Tab label="Profile" />
                    <Tab label="Work Experience" />
                    <Tab label="Education" />
                    <Tab label="Skills" />
                    <Tab label="Technical Expertise" />
                    <Tab label="Achievements" />
                </Tabs>
            </AppBar>           
            <TabPanel value={value} index={0} >
                <EditProfile />
            </TabPanel>
            <TabPanel value={value} index={1} >
                <EditWorkExperience />
            </TabPanel>
            <TabPanel value={value} index={2}>
                <EditEducation />
            </TabPanel>
            <TabPanel value={value} index={3}>
                <EditSkills />
            </TabPanel>
            <TabPanel value={value} index={4}>
                <EditExpertise />
            </TabPanel>
            <TabPanel value={value} index={5}>
                <EditAchievements />
            </TabPanel>
        </Box>
    );
}
