import { Box, Grid, Step, StepContent, StepLabel, Stepper } from "@mui/material";
import '../styles.css';
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";


export default function WorkExperience() {

    let model = ResumeDataSelector()?.data;
    return (<>
        {model?.id > 0 &&
            <Box className="LegendBox" component="fieldset">
                <legend className="LegendTextSmall">
                    <b>Work Experience</b>
                </legend>
                <Stepper orientation="vertical" >
                    {model.work.map((work, j) => <Step active={true} key={work.name}>
                        <StepLabel >
                            <Grid container xs={12} direction="row">
                                <Grid container item xs={12} justifyContent="space-between">
                                    <Grid item><p className="MediumText">{work.name}</p></Grid>
                                    <Grid item><p className="MediumText">{work.startDate}:{work.endDate}</p></Grid>
                                </Grid>
                                <Grid container item xs={12} justifyContent="space-between">
                                    <Grid item>{work.position}</Grid>
                                    <Grid item>{work.years}</Grid>
                                </Grid>
                            </Grid>
                        </StepLabel>
                        <StepContent style={{ padding: '0px auto', marginTop: '0px' }} >
                            <ul className="UlList" >
                                {work.summary.split('\n').map((m, i) =>
                                    (m.trim().length > 0)
                                        ? <li key={i}><p className="SmallText">{m}</p></li> : null
                                )}
                            </ul>
                        </StepContent>
                    </Step>)}
                </Stepper>
            </Box>

        }
    </>)
}

