import { Box, Chip, Stack } from "@mui/material";
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";


export function SkillsComponent() {
    let model = ResumeDataSelector()?.data;
    return (<>
        {model?.id > 0 &&
            <Box className="LegendBox" component="fieldset">
                <legend className="LegendTextSmall">
                    <b>Skills / Exposure</b>
                </legend>
                <Stack direction="row" sx={{ flexWrap: 'wrap', gap: 0.5 }}>
                    {model.skills.technologies.map((m, i) => {
                        return <Chip key={i} className="Chips" label={m.name} size="small" variant="outlined" />
                    })}
                    {model.skills.libraries.map((m, i) => {
                        return <Chip key={i} className="Chips" label={m.name} size="small" variant="outlined" />
                    })}
                    {model.skills.databases.map((m, i) => {
                        return <Chip key={i} className="Chips" label={m.name} size="small" variant="outlined" />
                    })}
                </Stack>
            </Box>}
    </>);
}

