import { Box, Divider, Stack } from "@mui/material";
import '../styles.css';
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";


export default function Education() {

    let model = ResumeDataSelector()?.data;
    return (<>
        {model?.id > 0 &&
            <Box className="LegendBox" style={{ textAlign: 'left' }} component="fieldset">
                <legend className="LegendTextSmall">
                    <b>Education</b>
                </legend>
                <Stack direction="column" spacing={1}>
                    {model.education.map((m, i) =>
                        <>
                            {i !== 0 ? <Divider></Divider> : null}
                            <Stack key={i} direction="column">
                                <Stack direction="row" justifyContent="space-between" >
                                    <p className="SmallText"><b>{m.institution}</b></p>
                                    <p className="SmallText"><b>{m.startDate}-{m.endDate}</b></p>
                                </Stack>
                                <Stack direction="row" justifyContent="space-between" >
                                    <p className="SmallText">{m.studyType}</p>
                                    <p className="SmallText">{m.score}</p>
                                </Stack>
                            </Stack>
                        </>
                    )}
                </Stack>
            </Box>
        }
    </>)
}