import { Paper, Stack, TextField } from "@mui/material";
import { useDispatch } from "react-redux";
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";
import { Action_Update_Data } from "../../../redux/reduxData/resumeData/actions";

export default function EditAchievements() {

    let model = ResumeDataSelector()?.data;
    let dispatch = useDispatch();

    const update = (target: string, value: any) => {
        let tempModel = { ...model };
        switch (target) {
            case "Achievements":
                tempModel.activities.achievements = value;
                break;
            case "Involvements":
                tempModel.activities.involvements = value;
                break;
        }
        dispatch(Action_Update_Data(tempModel));
    }

    return (
        <Paper elevation={5} sx={{ p: 3 }}>
            <Stack direction="column" spacing={2} >
                <TextField
                    fullWidth
                    label="Achievements"
                    value={model.activities?.achievements}
                    onChange={e => update("Achievements", e.target.value)}
                    variant="outlined"
                    multiline
                    rows={8}
                    maxRows={8}
                />
                <TextField
                    fullWidth
                    label="Involvements"
                    value={model.activities?.involvements}
                    onChange={e => update("Involvements", e.target.value)}
                    variant="outlined"
                    multiline
                    rows={8}
                    maxRows={8}
                />
            </Stack>
        </Paper>
    );
}