import { useDispatch } from "react-redux";
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";
import { Action_Update_Data } from "../../../redux/reduxData/resumeData/actions";
import { Button, Divider, Paper, Stack, TextField } from "@mui/material";
import { Education } from "../../../Models/ApplicationModel";

export default function EditEducation() {
    let model = ResumeDataSelector()?.data;
    let dispatch = useDispatch();

    const update = (target: string, index: number, value: any) => {
        let tempModel = { ...model };
        let education = tempModel.education[index];
        switch (target) {
            case "Institution":
                education.institution = value;
                break;
            case "StudyType":
                education.studyType = value;
                break;
            case "StartDate":
                education.startDate = value;
                break;
            case "EndDate":
                education.endDate = value;
                break;
            case "Score":
                education.score = value;
                break;
        }
        dispatch(Action_Update_Data(tempModel));
    }

    const addRow = () => {
        let tempModel = { ...model };
        tempModel.education.push(new Education());
        dispatch(Action_Update_Data(tempModel));
    }

    const deleteRow = (index: number) => {
        debugger;
        let tempModel = { ...model };
        tempModel.education.splice(index, 1);

        dispatch(Action_Update_Data(tempModel));
    }

    return (
        <Paper elevation={5} sx={{ p: 3 }}>
            <Stack direction={"column"} spacing={2} >
                {model.education.map((m, i) =>
                    <>
                        {i !== 0 ? <Divider></Divider> : null}
                        <Stack direction={"row"} spacing={2} >
                            <TextField label="Institution" value={m.institution} onChange={e => update("Institution", i, e.target.value)} />
                            <TextField label="StudyType" value={m.studyType} onChange={e => update("StudyType", i, e.target.value)} />
                            <TextField label="StartDate" value={m.startDate} onChange={e => update("StartDate", i, e.target.value)} />
                            <TextField label="EndDate" value={m.endDate} onChange={e => update("EndDate", i, e.target.value)} />
                            <TextField label="Score" value={m.score} onChange={e => update("Score", i, e.target.value)} />
                            <Button variant="outlined" size="small" onClick={() => deleteRow(i)}>Delete</Button>
                        </Stack>
                    </>)}
                <Button variant="contained" onClick={addRow}>Add row</Button>
            </Stack>
        </Paper>)
}