import { useDispatch } from "react-redux";
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";
import { Action_Update_Data } from "../../../redux/reduxData/resumeData/actions";
import { Paper, Stack, TextField } from "@mui/material";


export default function EditProfile() {

    let model = ResumeDataSelector()?.data;
    let dispatch = useDispatch();


    const update = (target: string, value: any) => {
        let tempModel = { ...model };
        switch (target) {
            case "Name":
                tempModel.basics.name = value;
                break;
            case "Role":
                tempModel.basics.label = value;
                break;
            case "TotalExperience":
                tempModel.basics.totalExp = value;
                break;
            case "RelevantExperience":
                tempModel.basics.relExp = value;
                break;
            case "Email":
                tempModel.basics.email = value;
                break;
            case "Image":
                tempModel.basics.image = value;
                break;
            case "Phone":
                tempModel.basics.phone = value;
                break;
            case "City":
                tempModel.basics.location.city = value;
                break;
            case "Summary":
                tempModel.basics.summary = value;
                break;
            case "Objective":
                tempModel.basics.objective = value;
                break;
        }
        dispatch(Action_Update_Data(tempModel));
    }


    const updateNetwork = (index: number, value: any) => {
        let tempModel = { ...model };
        let profile = tempModel.basics.profiles[index];
        profile.url = value;        
        dispatch(Action_Update_Data(tempModel));
    }

    return (
        <Paper elevation={5} sx={{ p: 3 }}>
            <Stack spacing={2}>
                <Stack direction={"row"} spacing={5} >
                    <Stack direction={"column"} spacing={2} >
                        <TextField label="Name" value={model.basics?.name} onChange={e => update("Name", e.target.value)} />
                        <TextField label="Role" value={model.basics?.label} onChange={e => update("Role", e.target.value)} />
                        <TextField label="Total Experience" value={model.basics?.totalExp} onChange={e => update("TotalExperience", e.target.value)} />
                        <TextField label="Relevant Experience" value={model.basics?.relExp} onChange={e => update("RelevantExperience", e.target.value)} />
                    </Stack>
                    <Stack direction={"column"} spacing={2}>
                        <TextField label="Email" value={model.basics?.email} onChange={e => update("Email", e.target.value)} />
                        <TextField label="Phone" value={model.basics?.phone} onChange={e => update("Phone", e.target.value)} />
                        <TextField label="City" value={model.basics?.location.city} onChange={e => update("City", e.target.value)} />
                        <TextField label="Image" value={model.basics?.image} onChange={e => update("Image", e.target.value)} />
                    </Stack>
                    <Stack direction={"column"} spacing={2}>
                        {model.basics.profiles.map((m,i) =>
                            <TextField style={{ width: "300px" }} label={m.network} value={m.url} onChange={e => updateNetwork(i, e.target.value)} />
                        )}
                    </Stack>
                </Stack >
                <Stack direction="column" spacing={2} >
                    <TextField
                        fullWidth
                        label="Objective"
                        value={model.basics?.objective}
                        onChange={e => update("Objective", e.target.value)}
                        variant="outlined"
                        multiline
                        rows={3}
                        maxRows={3}
                    />
                    <TextField
                        fullWidth
                        label="Summary"
                        value={model.basics?.summary}
                        onChange={e => update("Summary", e.target.value)}
                        variant="outlined"
                        multiline
                        rows={3}
                        maxRows={3}
                    />
                </Stack>
            </Stack>
        </Paper>)
}