import { useDispatch } from "react-redux";
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";
import { Box, Button, Grid, Paper, Rating, Stack, TextField } from "@mui/material";
import { Action_Update_Data } from "../../../redux/reduxData/resumeData/actions";
import { useState } from "react";
import { Framework, Language } from "../../../Models/ApplicationModel";

export default function EditExpertise() {

    const [language, setLanguage] = useState("");
    const [languageRating, setLanguageRating] = useState(1);

    const [framework, setFramework] = useState("");
    const [frameworkRating, setFrameworkRating] = useState(1);

    let model = ResumeDataSelector()?.data;
    let dispatch = useDispatch();

    const deleteClick = (target: string, index: number) => {
        let tempModel = { ...model };
        switch (target) {
            case "Languages":
                tempModel.skills.languages.splice(index, 1);
                break;
            case "Frameworks":
                tempModel.skills.frameworks.splice(index, 1);
                break;
        }
        dispatch(Action_Update_Data(tempModel));
    }

    const addClick = (target: string) => {
        let tempModel = { ...model };
        switch (target) {
            case "Languages":
                tempModel.skills.languages.push(new Language(language, languageRating));
                setLanguage("");
                setLanguageRating(1);
                break;
            case "Frameworks":
                tempModel.skills.frameworks.push(new Framework(framework, frameworkRating));
                setFramework("");
                setFrameworkRating(1);
                break;
        }
        dispatch(Action_Update_Data(tempModel));
    }

    return (
        <Paper elevation={5} sx={{ p: 3 }}>
            <Box sx={{ marginBottom: "20px" }} className="LegendBox" component="fieldset">
                <legend className="LegendTextBig">
                    <b>Languages</b>
                </legend>
                <Grid container direction={"column"} spacing={1}>
                    {model.skills.languages.map((m, i) => (
                        <Grid container item direction="row" spacing={1}>
                            <Grid item xs={2}>{m.name}</Grid>
                            <Grid item xs={1.5}>
                                <Rating name="read-only" size="medium" value={m.level} readOnly />
                            </Grid>
                            <Grid item xs={1}>
                                <Button onClick={() => deleteClick("Languages", i)} variant="outlined" size="small">
                                    Delete
                                </Button>
                            </Grid>
                        </Grid>
                    ))}
                    <Grid item>
                        <Stack direction="row" spacing={2}>
                            <TextField value={language} onChange={e => setLanguage(e.target.value)} size="small" inputProps={{ maxLength: 15 }} />
                            <TextField style={{ width: "100PX" }} type="number" value={languageRating} onChange={e => setLanguageRating(Number(e.target.value))} size="small" />
                            <Button variant="contained" onClick={() => addClick("Languages")}>Add</Button>
                        </Stack>
                    </Grid>
                </Grid>
            </Box>

            <Box className="LegendBox" component="fieldset">
                <legend className="LegendTextBig">
                    <b>Frameworks</b>
                </legend>
                <Grid container direction={"column"} spacing={1}>
                    {model.skills.frameworks.map((m, i) => (
                        <Grid container item direction="row" spacing={1}>
                            <Grid item xs={2}>{m.name}</Grid>
                            <Grid item xs={1.5}>
                                <Rating name="read-only" size="medium" value={m.level} readOnly />
                            </Grid>
                            <Grid item xs={1}>
                                <Button onClick={() => deleteClick("Frameworks", i)} variant="outlined" size="small">
                                    Delete
                                </Button>
                            </Grid>
                        </Grid>
                    ))}
                    <Grid item>
                        <Stack direction="row" spacing={2}>
                            <TextField value={framework} onChange={e => setFramework(e.target.value)} size="small" />
                            <TextField style={{ width: "100PX" }} type="number" value={frameworkRating} onChange={e => setFrameworkRating(Number(e.target.value))} size="small" />
                            <Button variant="contained" size="small" onClick={() => addClick("Frameworks")}>Add</Button>
                        </Stack>
                    </Grid>
                </Grid>
            </Box>
        </Paper>)
}