import { useDispatch } from "react-redux";
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";
import { Action_Update_Data } from "../../../redux/reduxData/resumeData/actions";
import { Box, Button, Divider, Paper, Stack, TextField } from "@mui/material";
import {  Work } from "../../../Models/ApplicationModel";


export default function EditWorkExperience() {

    let model = ResumeDataSelector()?.data;
    let dispatch = useDispatch();

    const update = (target: string, index: number, value: any) => {
        let tempModel = { ...model };
        let work = tempModel.work[index];
        switch (target) {
            case "Name":
                work.name = value;
                break;
            case "StartDate":
                work.startDate = value;
                break;
            case "EndDate":
                work.endDate = value;
                break;
            case "Position":
                work.position = value;
                break;
            case "Years":
                work.years= value;
                break;
            case "Summary":
                work.summary = value;
                break;
        }
        dispatch(Action_Update_Data(tempModel));
    }

    const addRow = () => {
        let tempModel = { ...model };
        tempModel.work.push(new Work());
        dispatch(Action_Update_Data(tempModel));
    }

    const deleteRow = (index: number) => {
        let tempModel = { ...model };
        tempModel.work.splice(index, 1);
        dispatch(Action_Update_Data(tempModel));
    }


    return (
        <Paper elevation={5} sx={{ p: 3 }}>
            {model.work.map((work, i) =>
                <>
                    {i !== 0 ? <Divider></Divider> : null}
                    <Box sx={{ p: 1, m: 1 }}>
                        <Stack direction="column" spacing={2}>
                            <TextField label="Summary"
                                value={work?.summary}
                                fullWidth
                                rows={6}
                                variant="outlined"
                                multiline
                                maxRows={6}
                                onChange={e => update("Summary", i, e.target.value)} />
                            <Stack direction={"row"} spacing={2} >
                                <TextField label="Name" value={work?.name} onChange={e => update("Name", i, e.target.value)} />
                                <TextField label="StartDate" value={work?.startDate} onChange={e => update("StartDate", i, e.target.value)} />
                                <TextField label="EndDate" value={work?.endDate} onChange={e => update("EndDate", i, e.target.value)} />
                                <TextField label="Position" value={work?.position} onChange={e => update("Position", i, e.target.value)} />
                                <TextField label="Years" value={work?.years} onChange={e => update("Years", i, e.target.value)} />
                                <Button variant="outlined" onClick={() => deleteRow(i)}> Delete</Button>
                            </Stack>
                        </Stack>
                    </Box>
                </>
            )}
            <Button variant="contained" fullWidth onClick={addRow}>Add row</Button>
        </Paper>
    );
}
