import { IAction } from "../types";
import { Actions, IResumeData } from "./types";
import BaseData from "../../../helpers/BaseData";

export let initialState: IResumeData = {
    data: BaseData()
};

export function ResumeDataReducer(state: IResumeData = initialState, action: IAction): IResumeData {
    switch (action.type) {
        case (Actions.UPDATE_DATA): {
            return {
                ...state,
                data: action.payload
            }
        }
        case Actions.RESTE_DATA: {
            return {
                ...state,
                data: BaseData()
            }
        }
        default: return state;
    }
}