import { useDispatch } from "react-redux";
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";
import { Button, Chip, Paper, Stack, TextField } from "@mui/material";
import { useState } from "react";
import { Action_Update_Data } from "../../../redux/reduxData/resumeData/actions";
import { Database, Library, Practice, Technology, Tool } from "../../../Models/ApplicationModel";
import SkillsCard from "../../atomic/SkillsCard/index";

export default function EditSkills() {

    const [pratice, setPratice] = useState("");
    const [tool, setTool] = useState("");
    const [technology, setTechnology] = useState("");
    const [database, setDatabase] = useState("");
    const [library, setLibrary] = useState("");

    let model = ResumeDataSelector()?.data;
    let dispatch = useDispatch();

    const deleteClick = (target: string, index: number) => {
        let tempModel = { ...model };
        switch (target) {
            case "Pratices":
                tempModel.skills.practices.splice(index, 1);
                break;
            case "Tools":
                tempModel.skills.tools.splice(index, 1);
                break;
            case "Technologies":
                tempModel.skills.technologies.splice(index, 1);
                break;
            case "Libraries":
                tempModel.skills.libraries.splice(index, 1);
                break;
            case "Databases":
                tempModel.skills.databases.splice(index, 1);
                break;
        }
        dispatch(Action_Update_Data(tempModel));

    }

    const addClick = (target: string) => {
        let tempModel = { ...model };
        switch (target) {
            case "Pratices":
                tempModel.skills.practices.push(new Practice(pratice, 1));
                setPratice("");
                break;
            case "Tools":
                tempModel.skills.tools.push(new Tool(tool, 1));
                setTool("");
                break;
            case "Technologies":
                tempModel.skills.technologies.push(new Technology(technology, 1));
                setTechnology("");
                break;
            case "Libraries":
                tempModel.skills.libraries.push(new Library(library, 1));
                setLibrary("");
                break;
            case "Databases":
                tempModel.skills.databases.push(new Database(database, 1));
                setDatabase("");
                break;
        }
        dispatch(Action_Update_Data(tempModel));
    }

    return (
        <Paper elevation={5} sx={{ p: 3 }}>
            <Stack direction="row" spacing={3}>
                <SkillsCard
                    title="Libraries"
                    content={<>
                        {model.skills.libraries.map((m, i) =>
                            <Chip key={i} onDelete={() => deleteClick("Libraries", i)} className="Chips" label={m.name} size="medium" />
                        )}
                    </>}
                    actions={<>
                        <TextField value={library} onChange={e => setLibrary(e.target.value)} size="small"></TextField>
                        <Button variant="contained" onClick={() => addClick("Libraries")}>Add</Button>
                    </>}
                />
                <SkillsCard
                    title="Technologies"
                    content={<>
                        {model.skills.technologies.map((m, i) =>
                            <Chip key={i} onDelete={() => deleteClick("Technologies", i)} className="Chips" label={m.name} size="medium" />
                        )}
                    </>}
                    actions={<>
                        <TextField value={technology} onChange={e => setTechnology(e.target.value)} size="small"></TextField>
                        <Button variant="contained" onClick={() => addClick("Technologies")}>Add</Button>
                    </>}
                />
                <SkillsCard
                    title="Databases"
                    content={<>
                        {model.skills.databases.map((m, i) =>
                            <Chip key={i} onDelete={() => deleteClick("Databases", i)} className="Chips" label={m.name} size="medium" />
                        )}
                    </>}
                    actions={<>
                        <TextField value={database} onChange={e => setDatabase(e.target.value)} size="small"></TextField>
                        <Button variant="contained" onClick={() => addClick("Databases")}>Add</Button>
                    </>}
                />
                <SkillsCard
                    title="Methodology/Practices"
                    content={<>
                        {model.skills.practices.map((m, i) =>
                            <Chip key={i} onDelete={() => deleteClick("Pratices", i)} className="Chips" label={m.name} size="medium" />
                        )}
                    </>}
                    actions={<>
                        <TextField value={pratice} onChange={e => setPratice(e.target.value)} size="small"></TextField>
                        <Button variant="contained" onClick={() => addClick("Pratices")}>Add</Button>
                    </>}
                />
                <SkillsCard
                    title="Tools"
                    content={<>
                        {model.skills.tools.map((m, i) =>
                            <Chip key={i} onDelete={() => deleteClick("Tools", i)} className="Chips" label={m.name} size="medium" />
                        )}
                    </>}
                    actions={<>
                        <TextField value={tool} onChange={e => setTool(e.target.value)} size="small"></TextField>
                        <Button variant="contained" onClick={() => addClick("Tools")}>Add</Button>
                    </>}
                />
            </Stack>
        </Paper>)
}