import { Avatar, Box, Grid, Stack } from "@mui/material";
import '../styles.css';
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";
import PhoneIphoneIcon from '@mui/icons-material/PhoneIphone';
import EmailIcon from '@mui/icons-material/Email';
import LocationOnIcon from '@mui/icons-material/LocationOn';

export default function ProfileComponent() {

    let model = ResumeDataSelector()?.data;
    return (<>
        {model?.id > 0 &&
            <Box className="LegendBox" component="fieldset">
                <legend className="LegendTextBig">
                    <b>{model.basics.name}</b>
                </legend>
                <Grid container item style={{ textAlign: 'left', maxHeight: "65px" }} xs={12} direction={"row"} justifyContent={"space-between"}>
                    <Grid container xs={6} item direction={"column"} spacing={2}>
                        <Grid item xs={4}>
                            <Avatar
                                alt={model.basics.name}
                                src={model.basics.image}
                                sx={{ height: "65px", width: "65px" }}
                            />
                        </Grid>
                        <Grid container item xs={8} direction={"column"} spacing={0.5}>
                            <Grid item><p className="MediumText" style={{ color: "blue" }}>{model.basics.label}</p></Grid>
                            <Grid item>Relevant experience: <b>{model.basics.relExp}</b></Grid>
                            <Grid item>Total experience: <b>{model.basics.totalExp}</b></Grid>
                        </Grid>
                    </Grid>
                    <Grid container item xs={5} direction={"column"} spacing={0.5}>
                        <Grid item>
                            <Stack direction="row" alignItems="center" gap={1}>
                                <PhoneIphoneIcon fontSize="small" />
                                {model.basics.phone}
                            </Stack>
                        </Grid>
                        <Grid item>
                            <Stack direction="row" alignItems="center" gap={1}>
                                <EmailIcon fontSize="small" />
                                {model.basics.email}
                            </Stack>
                        </Grid>
                        <Grid item>
                            <Stack direction="row" alignItems="center" gap={1}>
                                <LocationOnIcon fontSize="small" />
                                {model.basics.location.city}
                            </Stack>
                        </Grid>
                    </Grid>
                </Grid>
            </Box>

        }
    </>)
}
