import { Box, Grid } from "@mui/material";
import './styles.css';
import ResumeDataSelector from "../../redux/reduxSelectors/ResumeDataSelector";
import WorkExperience from "./workExperience/index";
import TechnicalExpertise from "./technicalExpertise";
import Education from "./education";
import ProfileComponent from "./profile";
import { MethodologyComponent, SkillsComponent, ToolsComponent } from "./skills";
import NetWorkComponent from "./network";
import {useStyles} from './mystyles';

export default function TemplateOne() {

    let model = ResumeDataSelector()?.data;
    const classes = useStyles();

    return (
        <>
            {model?.id > 0 &&
                <div className="print-visible" style={{ backgroundColor: 'white', overflow: "hidden", width: '800px', maxHeight: '1150px' }}>
                    <Grid container direction={"row"} spacing={1} justifyContent="space-between">
                        <Grid container item xs={8.5} direction={"column"} spacing={0.5}>
                            <Grid item>
                                <ProfileComponent />
                            </Grid>
                            <Grid item>
                                <WorkExperience></WorkExperience>
                            </Grid>
                            <Grid item >
                                <Box className={classes.legendBox} component="fieldset">
                                    <legend className={classes.legendTextSmall} >
                                        <b>Key Projects / Involvements</b>
                                    </legend>
                                    <ul className="UlList">
                                        {model.activities.involvements.split('\n').map((m, i) =>
                                            (m.trim().length > 0)
                                                ? <li key={i}><p className={classes.smallText} >{m}</p></li>
                                                : null
                                        )}
                                    </ul>
                                </Box>
                            </Grid>
                            <Grid item>
                                <Box className={classes.legendBox}  component="fieldset">
                                    <legend className={classes.legendTextSmall} >
                                        <b>Certificates and Awards</b>
                                    </legend>
                                    <ul className={classes.ulList} >
                                        {/* <p className="SmallText">{model.activities.achievements}</p> */}
                                        {model.activities.achievements.split('\n').map((m, i) =>
                                            (m.trim().length > 0)
                                                ? <li key={i}><p className={classes.smallText} >{m}</p></li>
                                                : null
                                        )}
                                    </ul>
                                </Box>
                            </Grid>
                        </Grid>
                        <Grid container item xs={3.5} direction={"column"} spacing={0.5}>
                            <Grid item><NetWorkComponent /></Grid>
                            <Grid item>
                                <Box className={classes.legendBox}  component="fieldset">
                                    <legend className={classes.legendTextSmall} >
                                        <b>Summary</b>
                                    </legend>
                                    <p className={classes.smallText} >{model.basics.summary}</p>
                                </Box>
                            </Grid>
                            <Grid item>
                                <Box className={classes.legendBox}  component="fieldset">
                                    <legend className={classes.legendTextSmall} >
                                        <b>Career Objective</b>
                                    </legend>
                                    <p className={classes.smallText} >{model.basics.objective}</p>
                                </Box>
                            </Grid>
                            <Grid item>
                                <TechnicalExpertise />
                            </Grid>
                            <Grid item>
                                <SkillsComponent />
                            </Grid>
                            <Grid item>
                                <MethodologyComponent />
                            </Grid>
                            <Grid item>
                                <ToolsComponent />
                            </Grid>
                            <Grid item>
                                <Education />
                            </Grid>
                        </Grid>
                    </Grid>
                </div >
            }
        </>);
}