import { Box, Grid, Stack } from '@mui/material';

interface SmallCardProps {
    title: string;
    content: JSX.Element;
    actions: JSX.Element;
}

export default function SkillsCard(props: SmallCardProps) {
    return (
        <Box sx={{ marginBottom: "20px", width: "20%" }} className="LegendBox" component="fieldset">
            <legend className="LegendTextBig">
                <b>{props.title}</b>
            </legend>
            <Grid container direction={"column"} spacing={3}>
                <Grid item>
                    <Stack direction="row" sx={{ flexWrap: 'wrap', gap: 0.5 }}>
                        {props.content}
                    </Stack>
                </Grid>
                <Grid item>
                    <Stack direction="row" spacing={2}>
                        {props.actions}
                    </Stack>
                </Grid>
            </Grid>
        </Box>
    );
}