export class ApplicationModel {
  id: number = -1;
  basics: Basics = new Basics();
  skills: Skills = new Skills();
  work: Work[] = [new Work()]
  education: Education[] = [new Education()]
  activities: Activities = new Activities();
  // volunteer: any[]
  // awards: any[]
}

export class Basics {
  name: string = "";
  label: string = "";
  image: string = "";
  email: string = "";
  phone: string = "";
  url: string = "";
  summary: string = "";
  location: Location = new Location();
  relExp: string = "";
  totalExp: string = "";
  objective: string = "";
  profiles: Profile[] = [];
}

export class Location {
  address: string = "";
  postalCode: string = "";
  city: string = "";
  countryCode: string = "";
  region: string = "";
}

export class Profile {
  network: string = "";
  username: string = "";
  url: string = "";
}

export class Skills {
  languages: Language[] = [];
  frameworks: Framework[] = [];
  libraries: Library[] = [];
  databases: Database[] = [];
  technologies: Technology[] = [];
  practices: Practice[] = [];
  tools: Tool[] = [];
}

export class Language {
  name: string = "";
  level: number = -1;
  constructor(name: string, level: number) {
    this.name = name;
    this.level = level;
  }
}

export class Framework {
  name: string = "";
  level: number = -1;
  constructor(name: string, level: number) {
    this.name = name;
    this.level = level;
  }
}

export class Library {
  name: string = "";
  level: number = -1;
  constructor(name: string, level: number) {
    this.name = name;
    this.level = level;
  }
}

export class Database {
  name: string = "";
  level: number = -1;
  constructor(name: string, level: number) {
    this.name = name;
    this.level = level;
  }
}

export class Technology {
  name: string = "";
  level: number = -1;
  constructor(name: string, level: number) {
    this.name = name;
    this.level = level;
  }
}

export class Practice {
  name: string = "";
  level: number = -1;
  constructor(name: string, level: number) {
    this.name = name;
    this.level = level;
  }
}

export class Tool {
  name: string = "";
  level: number = -1;

  constructor(name: string, level: number) {
    this.name = name;
    this.level = level;
  }
}

export class Work {
  name: string = "";
  position: string = "";
  url: string = "";
  startDate: string = "";
  endDate: string = "";
  years: string = "";
  summary: string = "";
}

export class Education {
  institution: string = "";
  url: string = "";
  studyType: string = "";
  area: string = "";
  startDate: string = "";
  endDate: string = "";
  score: string = "";
}

export class Activities {
  involvements: string = "";
  achievements: string = "";
}
