import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import { Box, Stack, TextField } from '@mui/material';
import ResumeDataSelector from '../../../redux/reduxSelectors/ResumeDataSelector';
import { useDispatch } from 'react-redux';
import { ApplicationModel } from '../../../Models/ApplicationModel';
import { Action_Reset_Data, Action_Update_Data } from '../../../redux/reduxData/resumeData/actions';


export default function Json() {
    const [selectedFile, setSelectedFile] = useState<File | null>(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);

    const handleFileInput = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (event.target.files) {
            setSelectedFile(event.target.files[0]);
        }
    };

    let model = ResumeDataSelector()?.data;
    let dispatch = useDispatch();

    const DownloadJSON = () => {
        const jsonData = JSON.stringify(model);
        const blob = new Blob([jsonData], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = "ResumeJson";
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
    };

    const HandleFileUpload = () => {
        if (selectedFile) {
            const reader = new FileReader();
            reader.onload = () => {
                const data = JSON.parse(reader.result as string) as ApplicationModel;
                dispatch(Action_Update_Data(data));
                setIsDialogOpen(false);
            };
            reader.readAsText(selectedFile);
        }
    };

    const ResetData = () => {
        dispatch(Action_Reset_Data());
    }

    const printClick = () => {
        window.print();
    }


    return (
        <>
            <Box display="flex" justifyContent="flex-end" alignItems="center"
                sx={{ mb: 1, paddingRight: 1, backgroundColor: "gray", width: '800px', height: '40px' }}>
                <Stack direction="row" spacing={2}>
                    <Button sx={{ color: '#87CEFA' }} size='small'
                        onClick={() => setIsDialogOpen(true)}>
                        Upload
                    </Button>
                    <Button sx={{ color: '#87CEFA' }} size='small'
                        onClick={DownloadJSON}>
                        Download
                    </Button>
                    <Button sx={{ color: '#87CEFA' }} size='small'
                        onClick={ResetData}>
                        Reset
                    </Button>
                    <Button sx={{ color: '#F7DC6F' }} size='small'
                        onClick={printClick}>
                        Print
                    </Button>
                </Stack>
            </Box>
            <Dialog open={isDialogOpen} onClose={() => setIsDialogOpen(false)}>
                <DialogTitle>Select a file to upload</DialogTitle>
                <DialogContent>
                    <TextField type="file" id="outlined-basic" variant="outlined" onChange={handleFileInput} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setIsDialogOpen(false)}>
                        Cancel
                    </Button>
                    <Button variant="contained" color="primary" onClick={HandleFileUpload}>
                        Upload
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    );
};
