import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import TemplateOne from './componenents/templateone';
import './App.css';
import { Button } from '@mui/material';
import EditResumeComponent from './componenents/editresume';
import { useState } from 'react';
import Json from './componenents/editresume/Json';
import HomeComponent from './componenents/home';

export default function App() {
  const [value, setValue] = useState(1);

  return (
    <Box>
      <div className='HideInPrint'>
        <AppBar component="nav" style={{ backgroundColor: 'black' }}>
          <Toolbar>
            <Typography onClick={() => setValue(0)}
              variant="h6"
              component="div"
              sx={{
                flexGrow: 1, display: { xs: 'none', sm: 'block' },
                cursor: 'pointer', // set cursor to pointer
                '&:hover': {
                  textDecoration: 'underline', // add underline on hover
                },
              }}>
              Resume Builder
            </Typography>
            <Box>
              <Button sx={{ color: '#fff' }} onClick={() => setValue(1)}>Preview</Button>
              <Button sx={{ color: '#fff' }} onClick={() => setValue(2)}>Edit Mode</Button>
            </Box>
          </Toolbar>
        </AppBar>
      </div>
      <Box sx={{ p: 1 }} >
        <div className='HideInPrint'>
          <Toolbar></Toolbar>
        </div>
        {value === 0 &&
          <HomeComponent />
        }
        {value === 1 &&
          <>
            <div className='HideInPrint'>
              <Json />
            </div>
            <TemplateOne></TemplateOne>
          </>
        }
        {value === 2 && <EditResumeComponent></EditResumeComponent>}
      </Box>
    </Box>
  );
}