import { Box, Grid, Rating } from "@mui/material";
import '../styles.css';
import ResumeDataSelector from "../../../redux/reduxSelectors/ResumeDataSelector";


export default function TechnicalExpertise() {

    let model = ResumeDataSelector()?.data;
    return (<>
        {model?.id > 0 &&
            <Box className="LegendBox" component="fieldset">
                <legend className="LegendTextSmall">
                    <b>Technical expertise</b>
                </legend>
                <Grid container direction={"column"} >
                    {model.skills.languages.map((m, i) => (
                        <Grid container item xs={12} direction="row">
                            <Grid item xs={6}>
                                <p className="SmallText">
                                    {m.name}
                                </p>
                            </Grid>
                            <Grid item xs={3}>
                                <Rating name="read-only" size="small" value={m.level} readOnly />
                            </Grid>
                        </Grid>
                    ))}
                    {model.skills.frameworks.map((m, i) => (
                        <Grid container item xs={12} direction="row">
                            <Grid item xs={6}>
                                <p className="SmallText">
                                    {m.name}
                                </p>
                            </Grid>
                            <Grid item xs={3}>
                                <Rating name="read-only" size="small" value={m.level} readOnly />
                            </Grid>
                        </Grid>
                    ))}
                </Grid>
            </Box>
        }
    </>)
}